<template>
	<div ref="ageChart" class="echart-box"></div>
</template>


<script>
	export default {
		name: 'Age',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.ageChart);
				if(!chart){
					chart = this.$echarts.init(this.$refs.ageChart, 'light');
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					title: {
						text: '各年龄段对比',
						left: 'center'
					},
					tooltip: {
						trigger: 'axis'
					},
					legend: {
						top: 'bottom',
						left: 'center'
					},
					xAxis: {
						name: '年龄(age)',
						type: 'category',
						data: ['0~18', '18~24', '25~29', '30~34', '35~39', '40~49', '50~∞', 'unknow']
					},
					yAxis: {
						name: '数据量(data)',
						type: 'value',
						min: 2285000,
						max: 2292000
					},
					series: [{
						name: '男性',
						type: 'line',
						data: this.list.slice(8, 16)
					},
					{
						name: '女性',
						type: 'line',
						data: this.list.slice(0, 8)
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'data/age.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list.push(resp.data[i][2]);
				}
				this.drawChart();
			})
		}
	}
</script>

